$beige: #f7f6f0;
$white: #ffffff;
$black: #262526;

.wp-block-create-block-dropdown-block {
	background-color: $beige;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 10vh 0;
	padding: 10vh 0;
}

.secondary-header {
	display: none;
}

.drop-drop-down-wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.drop-down-bar-1,
.drop-down-bar-2,
.drop-down-bar-3,
.drop-down-bar-4,
.drop-down-bar-5,
.drop-down-bar-6 {
	border: 1px solid black;
	width: 100%;
	height: 80px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-top: 4px;
}

.drop-down-bar-2:hover,
.drop-down-bar-1:hover,
.drop-down-bar-3:hover,
.drop-down-bar-4:hover,
.drop-down-bar-5:hover,
.drop-down-bar-6:hover {
	cursor: pointer;
}

