.third-header {
	position: absolute;
	left: 20vw;
	font-style: normal;
	font-weight: 500;
	font-size: 32.36px;
	line-height: 100%;
}

.drop-down-button-1,
.drop-down-button-2,
.drop-down-button-3,
.drop-down-button-4,
.drop-down-button-5,
.drop-down-button-6 {
	height: 50px;
	width: 50px;
	background-color: white;
	border-radius: 50%;
	margin-right: 30px;
	border: 0.5px solid black;
}

.drop-down-content-box-1,
.drop-down-content-box-2,
.drop-down-content-box-3,
.drop-down-content-box-4,
.drop-down-content-box-5,
.drop-down-content-box-6 {
	border: 0.5px solid black;
	width: 100%;
	height: auto;
	display: flex;
	align-items: center;
	justify-content: center;
	// display: none;
}

.drop-down-content-box-1 p,
.drop-down-content-box-2 p,
.drop-down-content-box-3 p,
.drop-down-content-box-4 p,
.drop-down-content-box-5 p,
.drop-down-content-box-6 p {
	font-style: normal;
	font-weight: normal;
	font-size: 20px;
	line-height: 160%;
	padding: 32px;
	letter-spacing: 0.02em;
}
